* {
  padding: 0;
  margin: 0; }

body {
  color: #333;
  font-size: 16px;
  min-width: 1100px;
  overflow-x: auto;
  background-color: #fff;
  -webkit-font-smoothing: antialiased;
  font-family: PingFang SC,Helvetica Neue,Arial,sans-serif; }

ul, li {
