  font-size: 13px;
  overflow-x: auto;
  font-weight: 400;
  line-height: 22px;
  border-radius: 3px;
  margin-bottom: 25px;
  position: relative;
  word-break: break-all;
  white-space: pre-wrap;
  color: #455a64;
  padding: 18px 10px 18px 20px;
  background-color: #f1f4f8;
  font-family: Source Code Pro,Monaco,Inconsolata,monospace; }

code:after {
  top: 5px;
  right: 10px;
  position: absolute;
  color: #ccc;
  font-size: 12px; }

pre {
  margin: 0; }

pre + pre {
  margin-top: -10px; }

code.language-html:after {
  content: "HTML"; }

code.language-javascript:after {
  content: "JS"; }

code.language-css:after {
  content: "CSS"; }

